* {
  box-sizing:border-box;
}

body,html {
  min-height:100%;
  width:100%;
  margin:0;
}

body {
  @include typeDefault;
}

#root {
  max-width:100%;
  height:auto;
  overflow-x:hidden;
}

.App {
  width:100%;
  height:calc(100vh - em(8));
  display:flex;
  flex-direction:column;
  justify-content: space-between;
  align-items: center;
  transition:left .5s ease-in-out;
  position:relative;
  @media screen and (max-width:1100px) {
    width:133.333vw;
    &.show-tray {
      left:-33.3333%;
    }
  }
}

%offsetPanelRight {
  padding-right:calc(1400px * .25 + 30px);
  padding-left:30px;
  @media screen and (max-width:1400px) {
    padding-right:calc(25% + 30px);
  }
}

%container {
  @extend %offsetPanelRight;
  width:100%;
  max-width:em(1400);
  margin-left:auto;
  margin-right:auto;
  position:relative;
}

.app-wrapper {
  position:relative;
  flex:auto;
  width:100%;
  display:flex;
  align-items:center;
  background:$light;
  height:calc(100vh - #{em(208)});
  @media screen and (max-width: 1100px) {
    height:calc(100vh - #{em(256)});
  }
}

.app-main {
  @extend %container;
  max-height:em(760);
  height:100%;
  padding-top:30px;
  padding-bottom:30px;
  z-index:3;
  pointer-events:none;
  .app-main-content {
    max-height:em(700);
    height:100%;
    position:relative;
    pointer-events:all;
  }
  .sankey-spacer {
    height:100%;
    width:100%;
    position:absolute;
  }
  .component-sankey {
    height:100%;
  }
}