.component-tray {
  position:absolute;
  right:0;
  top:0;
  width:25%;
  height:100%;
  background:#f8f6fa;
  font-size:14px;
  z-index:2;
  .tray__trigger {
    font-size:.825em;
    font-weight:900;
    position: absolute;
    top: 0;
    right: 100%;
    background:#f8f6fa;
    padding:.33em .66em;
    cursor:pointer;
    transform-origin:100% 100%;
    transform:rotate(-90deg);
    display:none;
    @media screen and (max-width:1100px) {
      display:block;
    }
  }
  .tray__content {
    padding:em(21) em(29);
    height:100%;
    overflow:auto;
  }
  .tray__entry {
    padding-top:1.2em;
    margin-top:1.2em;
    border-top:1px solid $dark;
    &:first-child {
      margin-top:0;
      border-top:none;
    }
    small {
      display:block;
      color:#777;
      font-size:.9em;
    }
  }
  .tray__entry__header {
    h3 {
      font-size:1em;
      margin:0;
      color:$dark;
    }
  }
  .tray__entry__content {
    h4 {
      margin-top:1.2em;
      margin-bottom:.5em;
      font-weight:600;
      font-style:italic;
      display:inline-block;
      color:#939393;
      font-size:em(13);
    }
  }
  .breakdown {
    list-style:none;
    margin:0;
    li,& {
      padding-left:0;
      margin-left:0;
    }
    li {
      display:flex;
      margin-bottom:.5em;
    }
    .value {
      width:em(70);
      flex:none;
    }
    label {
      flex:auto;
      padding-left:5%;
    }
  }
}