$control-w: em(31);
$control-w-inner: em(28);
$control-h: em(52);

.component-stickies {
  .stickies__control {
    display:flex;
    align-items:center;
    height:100%;
  }
  .stickies__label {
    @include typeFilterPre;
    margin-right:em(10,15);
  }
  .stickies__toggle {
    height:$control-w;
    width:$control-h;
    span {
      &:first-of-type {
        background:transparent;
        border:1px solid $light;
        border-radius:$control-w;
      }
      &:last-child {
        width:$control-w-inner;
        border-radius:$control-w-inner;
        &:after {
          content:"ON";
          position:absolute;
          top:50%;
          left:50%;
          transform:translate(-50%,-50%);
          font-size:em(9);
          color:$purple;
        }
      }
    }
    input[value="0"] {
      + span:first-of-type {
        //background:rgba($light,.5);
      }
      ~ span:last-child {
        left:2px;
        right:auto;
        &:after {
          content:"OFF";
        }
      }
    }
    input[value="1"] {
      ~ span:last-child {
        left:calc(100% - #{$control-w-inner} - 2px);
        right:auto;
        &:after {
          content:"ON";
        }
      }
    }
  }
}