.app-footer {
  @extend %component-contrast;
  height:em(100);
  padding-top:em(30);
  padding-bottom:em(30);
  flex:none;
  display: flex;
  align-items: center;
  width:100%;
  background:transparent linear-gradient(50deg, $blueDark 0%, $purple 100%) 0% 0% no-repeat padding-box;
  .footer__wrapper {
    padding:0 em(78);
    display:flex;
    justify-content:center;
    width:100%;
    > * {
      margin-left:em(85);
      &:first-child {
        margin-left:0;
      }
    }
  }
}